body {
    font-family: 'Slabo 27px', serif;
    background-color: #fff;
}
#app-body {
    margin-top: 80px;
    padding: 2vmin;
}
#side-navigation a {
    text-decoration: none;
}
.strike-through {
    text-decoration: line-through;
}
.highlight {
    font-size: 1.2em;
}
#product-thumbnail {
    height: 240px;
}
.aisles {
    display: initial;
}

#title {
    font-size: 5rem;
    font-family: 'Cinzel', serif;
}
#description {
    font-size: 2rem;
    font-family: 'Dancing Script', cursive;
    margin-top: 2vh;
    margin-bottom: 2vh;
}
#members p {
    font-size: 1.6rem;
    font-family: 'Cinzel', serif;
    text-transform: uppercase;
}
#members {
    margin-bottom: 4vh;
}
#continue-button {
    margin-bottom: 8vh;
}
